/* reset */
*, *::before, *::after {
    box-sizing: border-box;
  }

* {
    font-size: 14px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
    "Helvetica Neue", sans-serif;
    margin: 0;
}

body {
    line-height: 1.25;
    -webkit-font-smoothing: antialiased;
    cursor: default;
  }

  img, svg {
    display: block;
    max-width: 100%;
  }

  input, button, a, textarea {
    font: inherit;
  }

  p {
    overflow-wrap: break-word;
    text-wrap: pretty;
  }

  select {
    appearance: none;
  }

  input:-webkit-autofill {
    font-family: inherit;
    border: none;
    caret-color: white; 
    color: white;
    background: inherit;
    -webkit-font-feature-settings: off;
    -webkit-text-fill-color: black; 
    -webkit-background-clip: text; 
    border-bottom: 1.5px solid var(--accent-tint);

    }

    input, textarea{
        outline: none;
    }

    /* disable textarea stretching */
    textarea {
        resize: none;
    }

  /* Disable copying */
  .btn, .logo-text, .sidebar .space-icon, .svg, .sidebar .space-name {
  user-select: none;
  }

  /* hide scrollbar */
  ::-webkit-scrollbar {
    width: 0;  /* Remove scrollbar space */
    background: transparent;  /* Optional: just make scrollbar invisible */
}
  /* style */
  :root{
    --black: black;
    --dark-text: #1C192D;
    font-family: Arial, Helvetica, sans-serif;
    --background: white;
    --nav-height: 70px;
    --sidebar-width: 220px;
    --task-title-width: 60%;
    --accent-color: #EFEEFF;
    --accent-active: #d0ccff; 
    --accent-tint: #BCB9FF;
    --accent-hover: #bcb9ff62;
    --light-text: #545454;
    --underline:#e6e6e6;
    --grey-hover: #e6e6e652;
    font-family: Arial, Helvetica, sans-serif;
  }
/* main */
body{
    height: 100vh;
    width: 100vw;
    overflow-y: scroll;
    overflow-x: hidden;
}
.content-container{
    display: flex;
    gap:40px;
    height: calc(100% - var(--nav-height));
    width: 100%;
    position: relative;
}

/* navigation */
.navigation{
    display:flex;
    align-items: center;
    gap: 20px;
    height: var(--nav-height);
    background-color: var(--accent-color);
    border-bottom: 2px solid var(--accent-hover);
    padding-left: 10px;
  
  }

  .logo-container{
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
  }

  .logo-text{
    color: var(--dark-text);
    font-family: "Press Start 2P";
    font-size: 1.4rem;
  }
  .logo-img{
    filter: invert(8%) sepia(43%) saturate(776%) hue-rotate(209deg) brightness(87%) contrast(94%);
    width: 40px;
    height: 40px;
  }
/* sidebar */
.side-btn.btn.svg{
    width: 45px;
    height: 45px;
    filter: invert(71%) sepia(14%) saturate(3407%) hue-rotate(199deg) brightness(112%) contrast(101%);
}
.sidebar{
    display: flex;
    flex-direction: column;
    width: var(--sidebar-width);
    color: var(--light-text);
    transform: translateX(-100%);
    transition: transform ease-in-out 200ms;
    background-color: var(--accent-color);
    height: 100vh;
    padding: 5px;
    flex-shrink: 0; 
  position: absolute; 
  z-index: 10;
}
.sidebar.open{
    transform: translateX(0%);
    transition: transform ease-in-out 100ms;
    position: relative;
}
.sidebar:not(.open) + .todo-container {
    width: 100%;
    margin-left: calc(var(--sidebar-width) - 60px);
  }

.sidebar > div:first-child{
padding-bottom: 5px;
border-bottom: 2px solid var(--accent-hover);
}
.sidebar > div:last-of-type{
    padding-top: 5px;
 }
.space-row{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 10px 5px 5px;
    height: 36px;
    border-radius: 10px;
}
.space-row.active{
    color: var(--dark-text);
    font-weight: 500;
    background-color: var(--accent-active);
    transition: background-color 200ms ease-in-out;
}
.space-row .space-name{
    width: 90px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.space-row:hover{
   cursor: pointer;
   background-color: var(--accent-hover);
   transition: background-color 200ms ease-in-out;
}
.sidebar .btn:hover{
   background-color: var(--accent-hover);
   transition: background-color 200ms ease-in-out;
}
.space-row > div:first-child{
display: flex;
gap: 5px;
align-items: center;
justify-content: center;
}
.add-space.svg.btn{
    filter: invert(71%) sepia(14%) saturate(3407%) hue-rotate(199deg) brightness(112%) contrast(101%);
}
.space-row .emoji{
    font-family: "Noto Emoji";
}

/* tasks info */
.todo-container{
    display: flex;
    width:calc(100% - var(--sidebar-width));
    flex-direction: column;
    flex: 1 1 auto;
    margin: 35px 0px 0px 40px;
    padding-bottom: 20px;
    padding-right: 100px;
}
.header-row{
    width: calc(100% - 108px);
}
.header-row .space-icon.emoji{
    font-size: 4rem;
    margin-bottom: 5px;
}
.space-header{
    font-size: 1.5rem;
    font-weight: 600;
    padding-bottom: 10px;
    padding-left: 10px;
    border-bottom: 1.5px solid var(--underline);
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.counter-container{
    padding-top: 10px;
    font-size: 1.1rem;
}

.tasks-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
}

.task-title{
    min-width:60%;
}
.task-name{
    min-width: 90%;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.task-row{
    width: 100%;
    height: 47px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap:10px;
    padding: 5px;
}

.tasks-container .task-row{
    position: relative;
    cursor: pointer;
}
.tasks-container .task-row::after{
    position: absolute;
    bottom: 0;
    left: 0;
    content: " ";
    width: calc(100% - 108px);
    height: 1px;
    background-color: var(--underline);
    border-bottom: 1.5px solid var(--underline);
}

.task-row > div{
    display: flex;
    align-items: center;
    gap: 5px;
}

.task-row .btn{
    opacity: 0;
}
.task-row:hover .btn{
    opacity: 1;
    transition: opacity 200ms ease-in-out;
}

.tasks-container .task-btns{
    width: var(--task-btns-width);
}
.tasks-container .task-date{
    color: var(--light-text);
}
/* dialog */
 dialog{
    padding: 0;
    align-self: center;
    justify-self: center;
    width: 400px;
    height: 600px;
    border-radius: 15px;
    border: none;
    overflow: hidden;
 }
 .dialog-content{
    padding: 20px;
    align-self: center;
    justify-self: center;
    width: 100%;
 }
 .task-card{
    width: 400px;
    height: 400px;
 }

 .card-header{
    display: flex;
    gap: 5px;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;
    border-bottom: 1.5px solid var(--accent-tint);
 }
 .card-header h2{
    font-size: 0.9rem;
    width: 350px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
 }
 .task-card h1{
    font-size: 1.1rem;
    width: 200px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
 }

 .card-task-description{
    line-height: 1.3;
    padding: 20px;
    height: 250px;
    border-radius: 15px;
    background-color: var(--accent-hover);
    overflow-y: scroll;
    overflow-x: hidden;
    word-break: pretty;
 }
 ::backdrop {
    background-color:rgba(0,0,0,0.5);
  }

.card-btns{
    display: flex;
}
.card-main{
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.card-task-info{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.card-task-info > div{
    display: flex;
    align-items: center;
    gap: 8px;
}
/* task form */
#selectedEmoji{
    display: flex;
    justify-content: center;
    font-size: 1.7rem;
    width: 32px;
    border: none;
    justify-self:flex-start;
}
#emojiPicker{
    width: 300px;
    height: 300px;
}
.emoji-row{
    display: flex;
    flex-direction: column;
}
.emoji-row div{
 display: flex;
 align-items: center;
}
.emoji-row div{
    align-self: flex-start;
   }

form{
    display: flex;
    flex-direction: column;
    padding: 10px;
    gap: 20px;
}
form legend {
    padding: 10px 0px 5px 0px;
    margin-bottom: 5px;
    border-radius: 15px;
    text-align: center;
    background-color: var(--accent-hover);
    font-size: 1.1rem;
    font-weight: 700;
}
.form-row{
    display: flex;
    align-items: center;
    gap: 10px;
}
form label{
    font-weight: 500;
}
form input{
    width: 250px;
    height: 30px;
    border: none;
    border-bottom: 1.5px solid var(--accent-tint);
}

button[class*="form-btn"]{
    border-radius: 8px;
    background-color: var(--accent-hover);
    border: none;
    width: 120px;
    padding: 8px 10px;
    align-self: center;
}
button[class*="form-btn"]:hover{
    cursor: pointer;
    background-color: var(--accent-tint);
    transition: background-color 200ms ease-in;
}

/* task form */
form select{
    width: 250px;
    height: 30px;
    border: none;
    padding: 3px;
    border-bottom: 1.5px solid var(--accent-tint);
    outline: none;
}
.notes-label{
    align-self: flex-start;
}

textarea{
    width: 250px;
    height: 200px;
    border: none;
    outline: none;
    background-color: var(--accent-hover);
    border-radius: 15px;
    padding: 15px;
}
.svg.btn{
    background-color: transparent;
    border-radius: 30px;
    border: 1px solid transparent;
    width: 36px;
    height: 36px;
    filter: invert(0.60);
}
.btn:hover{
    cursor:pointer;
    transform: translateY(1px);
}
.btn:active{
    transform: translateY(-1px);
}
.done-btn{
    width: 15px;
    height: 15px;
    font-size: 0.8rem;
    background-color: transparent;
    border: 1px solid var(--light-text);
    border-radius: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2px; 
    flex: 0 0 auto;
    color: black;
}
.done-btn:hover{
    cursor: pointer;
    content: "";
}
.emoji{
    font-size: 1.5rem;
}
.task-priority.emoji{
    font-size: 1rem;
    width: 20px;
}

.space-row .btn{
opacity: 0;
transition: opacity ease-in-out 100ms;
}

.space-row:hover .btn{
opacity: 1;
}

@media(max-width:900px){
    .sidebar:not(.open) + .todo-container {
        width: 100%;
        margin-left: calc(var(--sidebar-width) - 180px);
    }
    .todo-container {
        padding-right: 20px;
    }
}

@media(max-width:800px){
    .sidebar {
        width: 100%;
        max-width: 100%;
        margin-right: 0;
        top: var(--nav-height);
        position: fixed;
        left: 0;
        overflow-y: auto;
      }
      .task-title{
        min-width: 40%;
      }
      .sidebar.open {
        position: fixed;
      }
      .sidebar.open + .todo-container {
        visibility: hidden; 
      }

    .content-container{
        gap: 0px;
    }
    .space-row .space-name, .space-row .space-name.active{
        width: 400px;
    }

    .sidebar:not(.open) + .todo-container {
        width: 100%;
        margin-left: calc(var(--sidebar-width) - 190px);
    }
    .todo-container {
        padding-right: 10px;
    }
}
@media(max-width:500px){
    .space-row .space-name, .space-row .space-name.active{
        width: 300px;
    }
    .task-btns{
        display: none;
    }
    .tasks-container .task-row::after{
        position: absolute;
        bottom: 0;
        left: 0;
        content: " ";
        width: 100%;
        height: 1px;
        background-color: var(--underline);
        border-bottom: 1.5px solid var(--underline);
    }
    .task-row {
        padding-right: 20px;
    }
