t;\r\n    border: none;\r\n    caret-color: white; \r\n    color: white;\r\n    background: inherit;\r\n    -webkit-font-feature-settings: off;\r\n    -webkit-text-fill-color: black; \r\n    -webkit-background-clip: text; \r\n    border-bottom: 1.5px solid var(--accent-tint);\r\n\r\n    }\r\n\r\n    input, textarea{\r\n        outline: none;\r\n    }\r\n\r\n    /* disable textarea stretching */\r\n    textarea {\r\n        resize: none;\r\n    }\r\n\r\n  /* Disable copying */\r\n  .btn, .logo-text, .sidebar .space-icon, .svg, .sidebar .space-name {\r\n  user-select: none;\r\n  }\r\n\r\n  /* hide scrollbar */\r\n  ::-webkit-scrollbar {\r\n    width: 0;  /* Remove scrollbar space */\r\n    background: transparent;  /* Optional: just make scrollbar invisible */\r\n}\r\n  /* style */\r\n  :root{\r\n    --black: black;\r\n    --dark-text: #1C192D;\r\n    font-family: Arial, Helvetica, sans-serif;\r\n    --background: white;\r\n    --nav-height: 70px;\r\n    --sidebar-width: 220px;\r\n    --task-title-width: 60%;\r\n    --accent-color: #EFEEFF;\r\n    --accent-active: #d0ccff; \r\n    --accent-tint: #BCB9FF;\r\n    --accent-hover: #bcb9ff62;\r\n    --light-text: #545454;\r\n    --underline:#e6e6e6;\r\n    --grey-hover: #e6e6e652;\r\n    font-family: Arial, Helvetica, sans-serif;\r\n  }\r\n/* main */\r\nbody{\r\n    height: 100vh;\r\n    width: 100vw;\r\n    overflow-y: scroll;\r\n    overflow-x: hidden;\r\n}\r\n.content-container{\r\n    display: flex;\r\n    gap:40px;\r\n    height: calc(100% - var(--nav-height));\r\n    width: 100%;\r\n    position: relative;\r\n}\r\n\r\n/* navigation */\r\n.navigation{\r\n    display:flex;\r\n    align-items: center;\r\n    gap: 20px;\r\n    height: var(--nav-height);\r\n    background-color: var(--accent-color);\r\n    border-bottom: 2px solid var(--accent-hover);\r\n    padding-left: 10px;\r\n  \r\n  }\r\n\r\n  .logo-container{\r\n    width: 90%;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    gap: 10px;\r\n  }\r\n\r\n  .logo-text{\r\n    color: var(--dark-text);\r\n    font-family: \"Press Start 2P\";\r\n    font-size: 1.4rem;\r\n  }\r\n  .logo-img{\r\n    filter: invert(8%) sepia(43%) saturate(776%) hue-rotate(209deg) brightness(87%) contrast(94%);\r\n    width: 40px;\r\n    height: 40px;\r\n  }\r\n/* sidebar */\r\n.side-btn.btn.svg{\r\n    width: 45px;\r\n    height: 45px;\r\n    filter: invert(71%) sepia(14%) saturate(3407%) hue-rotate(199deg) brightness(112%) contrast(101%);\r\n}\r\n.sidebar{\r\n    display: flex;\r\n    flex-direction: column;\r\n    width: var(--sidebar-width);\r\n    color: var(--light-text);\r\n    transform: translateX(-100%);\r\n    transition: transform ease-in-out 200ms;\r\n    background-color: var(--accent-color);\r\n    height: 100vh;\r\n    padding: 5px;\r\n    flex-shrink: 0; \r\n  position: absolute; \r\n  z-index: 10;\r\n}\r\n.sidebar.open{\r\n    transform: translateX(0%);\r\n    transition: transform ease-in-out 100ms;\r\n    position: relative;\r\n}\r\n.sidebar:not(.open) + .todo-container {\r\n    width: 100%;\r\n    margin-left: calc(var(--sidebar-width) - 60px);\r\n  }\r\n\r\n.sidebar > div:first-child{\r\npadding-bottom: 5px;\r\nborder-bottom: 2px solid var(--accent-hover);\r\n}\r\n.sidebar > div:last-of-type{\r\n    padding-top: 5px;\r\n }\r\n.space-row{\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    padding: 5px 10px 5px 5px;\r\n    height: 36px;\r\n    border-radius: 10px;\r\n}\r\n.space-row.active{\r\n    color: var(--dark-text);\r\n    font-weight: 500;\r\n    background-color: var(--accent-active);\r\n    transition: background-color 200ms ease-in-out;\r\n}\r\n.space-row .space-name{\r\n    width: 90px;\r\n    overflow: hidden;\r\n    white-space: nowrap;\r\n    text-overflow: ellipsis;\r\n}\r\n.space-row:hover{\r\n   cursor: pointer;\r\n   background-color: var(--accent-hover);\r\n   transition: background-color 200ms ease-in-out;\r\n}\r\n.sidebar .btn:hover{\r\n   background-color: var(--accent-hover);\r\n   transition: background-color 200ms ease-in-out;\r\n}\r\n.space-row > div:first-child{\r\ndisplay: flex;\r\ngap: 5px;\r\nalign-items: center;\r\njustify-content: center;\r\n}\r\n.add-space.svg.btn{\r\n    filter: invert(71%) sepia(14%) saturate(3407%) hue-rotate(199deg) brightness(112%) contrast(101%);\r\n}\r\n.space-row .emoji{\r\n    font-family: \"Noto Emoji\";\r\n}\r\n\r\n/* tasks info */\r\n.todo-container{\r\n    display: flex;\r\n    width:calc(100% - var(--sidebar-width));\r\n    flex-direction: column;\r\n    flex: 1 1 auto;\r\n    margin: 35px 0px 0px 40px;\r\n    padding-bottom: 20px;\r\n    padding-right: 100px;\r\n}\r\n.header-row{\r\n    width: calc(100% - 108px);\r\n}\r\n.header-row .space-icon.emoji{\r\n    font-size: 4rem;\r\n    margin-bottom: 5px;\r\n}\r\n.space-header{\r\n    font-size: 1.5rem;\r\n    font-weight: 600;\r\n    padding-bottom: 10px;\r\n    padding-left: 10px;\r\n    border-bottom: 1.5px solid var(--underline);\r\n    overflow: hidden;\r\n    white-space: nowrap;\r\n    text-overflow: ellipsis;\r\n}\r\n\r\n.counter-container{\r\n    padding-top: 10px;\r\n    font-size: 1.1rem;\r\n}\r\n\r\n.tasks-container{\r\n    width: 100%;\r\n    display: flex;\r\n    flex-direction: column;\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.task-title{\r\n    min-width:60%;\r\n}\r\n.task-name{\r\n    min-width: 90%;\r\n    overflow: hidden;\r\n    white-space: nowrap;\r\n    text-overflow: ellipsis;\r\n}\r\n.task-row{\r\n    width: 100%;\r\n    height: 47px;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    gap:10px;\r\n    padding: 5px;\r\n}\r\n\r\n.tasks-container .task-row{\r\n    position: relative;\r\n    cursor: pointer;\r\n}\r\n.tasks-container .task-row::after{\r\n    position: absolute;\r\n    bottom: 0;\r\n    left: 0;\r\n    content: \" \";\r\n    width: calc(100% - 108px);\r\n    height: 1px;\r\n    background-color: var(--underline);\r\n